.root {
  padding: 12px;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
}

.currency-rate-string {
  display: flex;
}

.new-currency1,
.new-currency2 {
  display: flex;
  justify-items: center;
  justify-content: space-around;
}

.currency-rate-string-text {
  font-size: 25px;
  margin-right: 4px;
}

.exhcange-rates,
.right-block {
  min-width: 420px;
  margin-top: 20px;
  flex-grow: 1;
}

.currency-rate-string {
  padding: 16px 0;
}

.input {
  max-width: 60px;
}

.result-row {
  padding: 8px 0;
}

.rate-row {
  display: flex;
}

.rate-row-from {
  font-size: 25px;
  width: 150px;
  text-align: start;
}

.rate-row-values {
  margin-left: 4px;
}

.select {
  min-width: 130px;
}
